.wrapper {
    display: flex;
    flex-direction: row-reverse;
    margin-top: 20px;
}

.userButton {
    height: 50px;
    padding: 0 23px 0 27px;
    border: 0;
    border-radius: 30px;
    outline: 0;
    font-size: 18px;
    text-align: center;
    background-color: #303030;
    color: white;
    position: relative;
    cursor: pointer ;
}

.userButton__icon {
    margin-left: 11px;
}