@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Rubik&display=swap');

*, *:after, *:before {
    box-sizing: border-box;
}

body {
    font-family: 'Roboto', sans-serif;
    background-color: #19191B;
    color: white;
}

.app {
    padding: 0 75px;
}

@media (max-width: 992px) {
    .app {
        padding: 10px;
    }
}