.wrapper {
    height: 100%;
    opacity: 1;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
}

.hidden {
    height: 0;
    overflow: hidden;
    transition: all 300ms ease;
}

.list {
    width: 100%;
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style: none;
}

.list:before {
    content: '';
    display: block;
    position: relative;
    bottom: -5px;
    width: 10px;
    height: 10px;
    background-color: #ffffff;
    margin: 0 0 0 25px;
    transform: rotate(45deg);
    z-index: -1;
}

.item {
    height: 50px;
    width: 100%;
    padding: 0 0 0 10px;
    border: 1px solid white;
    font-size: 18px;
    line-height: 50px;
    text-align: left;
    color: #19191B;
    background-color: #ffffff;
    cursor: pointer;
}

.item:not(:last-child) {
    border-bottom: 0;
}

.item:hover {
    background-color: #19191B;
    color: #ffffff;
}