.wrapper {
    font-family: 'Rubik', sans-serif;
    width: 100%;
    margin-top: 25px;
    padding: 0 24px;
    border-radius: 16px;
    background-color: #303030;
}

.head {}

.body {}

.row {
    text-align: center;
    height: 61px;
}

.row:not(:last-child) > .cell {
    border-bottom: 1px solid rgba(255, 255, 255, .1);
}

.head .row {
    height: 71px;
}

.head .cell {
    border-bottom: 1px solid rgba(255, 255, 255, .1);
    font-size: 14px;
}

.row > .cell:nth-child(even) {
    background-color: rgba(255, 255, 255, .05);
}

.cell {
    min-width: 180px;
    width: 100%;
    font-size: 16px;
    line-height: 19px;
    transition: all 150ms ease;
}

.cell:first-child {
    text-align: left;
}

.head .cell {
    color: rgba(255, 255, 255, .5);
}

.body .cell:first-child {
    background: linear-gradient(86.11deg, #FFCB44 -2.44%, #F89521 100.95%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.cell__logo {
    width: 20px;
    height: 20px;
    margin: 0 14px 0 9px;
    vertical-align: bottom;
}

@keyframes fall {
    0% {
        text-shadow: 0 0 2px #BFE2FF,
        0 0 3px #BFE2FF,
        0 0 4px #BFE2FF,
        0 0 5px #BFE2FF,
        0 0 8px #BFE2FF;
    }

    2% {
        text-shadow: 0 0 2px rgba(223, 40, 40, .7),
        0 0 3px rgba(223, 40, 40, .7),
        0 0 4px rgba(223, 40, 40, .7),
        0 0 5px rgba(223, 40, 40, .7),
        0 0 8px rgba(223, 40, 40, .7);
    }

    100% {
        text-shadow: 0 0 0 rgba(223, 40, 40, .2),
        0 0 0 rgba(223, 40, 40, .2),
        0 0 0 rgba(223, 40, 40, .2),
        0 0 0 rgba(223, 40, 40, .2);
    }
}

.fall {
    color: #DF2828; /* rgb(223, 40, 40) */
    animation: fall 1000ms linear;
}

@keyframes race {
    0% {
        text-shadow: 0 0 2px #BFE2FF,
        0 0 3px #BFE2FF,
        0 0 4px #BFE2FF,
        0 0 5px #BFE2FF,
        0 0 8px #BFE2FF;
    }

    2% {
        text-shadow: 0 0 2px rgba(76, 170, 60, .7),
        0 0 3px rgba(76, 170, 60, .7),
        0 0 4px rgba(76, 170, 60, .7),
        0 0 5px rgba(76, 170, 60, .7),
        0 0 8px rgba(76, 170, 60, .7);
    }

    100% {
        text-shadow: 0 0 0 rgba(76, 170, 60, .2),
        0 0 0 rgba(76, 170, 60, .2),
        0 0 0 rgba(76, 170, 60, .2),
        0 0 0 rgba(76, 170, 60, .2);
    }
}

.race {
    color: #4CAA3C;
    animation: race 1000ms linear;
}